body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.error {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

h1 {
  font-size: 45px;
  color: #3D393B;
  font-weight: bold;
  letter-spacing: -1px;
  line-height: 0.9;
  text-align: center;
  padding-bottom: 20px; }

h2 {
  font-size: 30px;
  font-weight: 300;
  text-align: center; }

.button {
  min-width: 190px;
  margin-left: 65%; }

@media screen and (max-width: 630px) {
  h1 {
    font-size: 30px; }
  h2 {
    font-size: 20px; }
  .button {
    margin-left: 50%; }
  .error {
    top: 30%;
    left: 20%;
    -webkit-transform: translate(-15%, -50%);
            transform: translate(-15%, -50%); } }

.buttonsMediaQueries {
  display: grid; }

.gridMediaQueries {
  display: flex;
  padding-top: 10px;
  align-items: center; }

@media screen and (max-width: 815px) {
  .gridMediaQueries {
    display: block; }
  .buttonsMediaQueries {
    display: inline;
    text-align: center; } }

